.home{
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.intro{
    max-width: 540px;
    text-align: center;
    z-index: 1;
}

.toggle-btn{
    position: absolute;
    top: 2%;
    right: -10%;
    padding: 8px 10px;
    background-color: rebeccapurple;
    color: white;
    border-radius: 5px;
}

.toggle-img{
    border-radius: 50%;
    background-color:white;
    position: absolute;
    top: 2%;
    right: 2%;
    padding: 8px 10px;
    z-index: 100;
    cursor: pointer;
}

.toggle-dark{
    background-color: #1e2a47;
}

.bg-dark{
    background-color: #1e2a47;
}

.home__img{
    width: 175px;
    margin-bottom: 1.5rem;
}

.home__name{
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
    margin-bottom: 0.5rem;
}

.home__socials{
    display: flex;
    column-gap: 1.75rem;
    justify-content: center;
    margin: 1.5rem 0;
}

.home__social-link{
    color: var(--title-color);
    font-size: 1.3rem;
    transition: 0.3s ;
}

.home__social-link:hover{
    color: hsl(43, 100%, 68%);
}

span{
    font-size: 1.5rem;
}

.scroll__down{
    position: absolute;
    bottom: 2rem;
    left: 0;
    width: 100%;
}

.home__scroll-name{
    font-size: var(--small-font-size);
}

.mouse{
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0%{
        top: 29%;
    }
    15%{
        top: 50%;
    }
    50%{
        top: 50%;
    }
    100%{
        top: 29%;
    }
}

.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

.shapes{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.shape {
    position: absolute;
}

.s1{
    left: 2%;
    top: 10%;
}

.s2{
    left: 18%;
    top: 30%;
}

.s3{
    left: 5%;
    bottom: 30%;
}

.s4{
    left: 2%;
    bottom: 10%;
}

.s5{
    left: 44%;
    top: 10%;
}

.s6{
    left: 36%;
    bottom: 10%;
}

.s7{
    top: 20%;
    right: 25%;
}

.s8{
    right: 24%;
    bottom: 20%;
}

.s9{
    right: 2%;
    top: 10%;
}

.s10{
    top: 45%;
    right: 11%;
}

.s11{
    bottom: 10%;
    right: 2%;
}



@media screen and (max-width: 1024px) {
    .home__social-link{
      font-size: 1.125rem;
    }
    .home__img{
        width: 100px;
    }
    .toggle-img{
        right: 3%;
    }
  }

@media screen and (max-width: 768px) {
    .btn{
        font-size: 1rem;
        padding: 0.5rem 1rem;
        line-height: 1;
    }
    .toggle-img{
        right: 3%;
    }
}

@media screen and (max-width: 315px) {
    .home__name{
        font-size: 24px;
    }
    .home__education{
        font-size: 20px;
    }
    .home__social-link{
        font-size: 1rem;
      }
}















